.App {
  text-align: center;
  background-image: url(../src/assets/img/Tiendebytte_frokost_bg.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: hidden;
  color: white;
}
h1,
h2,
h3,
h4,
h5 {
  color: white;
}
@media only screen and (max-width: 850px) {
  .BillBord {
    width: 80%;
    height: 80%;
  }
}

@media only screen and (max-width: 650px) {
  .BillBord {
    width: 95%;
    height: 95%;
  }
  h1 {
    font-size: 1.5em;
  }
  h2 {
    font-size: 1em;
  }
  h4 {
    font-size: 0.7em;
  }
}
