.BillBord {
  background-color: rgba(214, 214, 214, 0.814);
  min-width: 60vw;
  min-height: 80%;
  max-height: 95vh;
  margin: auto;
  border-radius: 20px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: block;
  overflow: auto;
  overflow-x: hidden;
}
.headerText {
  flex: wrap;
}
.PrimaryTextColor {
  color: rgba(0, 0, 0, 0.7);
  flex: wrap;
  text-align: center;
}
.AlertBox {
  background-color: rgba(244, 23, 32, 0.6);
  width: 25vw;
  height: 25vh;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}
.AlertBoxSuccess {
  background-color: rgba(12, 112, 12, 0.6);
  border-radius: 10px;
  box-shadow: darkblue;
  min-width: 25vw;
  min-height: 25vh;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  overflow: auto;
}
.inputFields {
  color: white !important;
}
.MuiInputBase-input {
  color: 'white';
}
